@use 'base/variables' as v;
@use 'base/mixins' as m;

/////////////////////////////////////////////////////////
// DEFAULTS SETTINGS
/////////////////////////////////////////////////////////

.glider-contain {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
}
.glider {
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: none;
  transform: translateZ(0);
}
.glider-track {
  transform: translateZ(0);
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  z-index: 1;
}
.glider.draggable {
  user-select: none;
  cursor: -webkit-grab;
  cursor: grab;
}
.glider.draggable .glider-slide img {
  user-select: none;
  pointer-events: none;
}
.glider.drag {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
.glider-slide {
  user-select: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  width: 100%;
  padding: 5rem 2rem;
  a {
    margin: 0;
    padding: 0;
  }
}
.glider-slide img {
  max-width: 100%;
}
.glider::-webkit-scrollbar {
  opacity: 0;
  height: 0;
}
.glider-prev,.glider-next {
  user-select: none;
  position: absolute;
  outline: none;
  background: none;
  padding: 0;
  z-index: 2;
  font-size: 4rem;
  text-decoration: none;
  left: -23px;
  border: 0;
  top: calc(50% - 2rem);
  cursor: pointer;
  color: #666;
  opacity: 1;
  line-height: 1;
  transition: opacity .5s cubic-bezier(.17,.67,.83,.67), color .5s cubic-bezier(.17,.67,.83,.67);
}
.glider-prev:hover,
.glider-next:hover,
.glider-prev:focus,
.glider-next:focus {
  color: #a89cc8;
}
.glider-next {
  right: -23px;
  left: auto;
}
.glider-next.disabled,
.glider-prev.disabled {
  opacity: .25;
  color: #666;
  cursor: default;
}
.glider-slide {
  min-width: 150px;
}
.glider-hide {
  opacity: 0;
}
.glider-dots {
  user-select: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  padding: 0;
}
.glider-dot {
  border: 0;
  padding: 0;
  user-select: none;
  outline: none;
  display: block;
  cursor: pointer;
  color: #ccc;
  border-radius: 999px;
  background: #ccc;
  width: 12px;
  height: 12px;
  margin: 7px;
}
.glider-dot:hover,
.glider-dot:focus,
.glider-dot.active {
  background: #a89cc8;
}
@media(max-width: 36em){
  .glider::-webkit-scrollbar {
    opacity: 1;
    -webkit-appearance: none;
    width: 7px;
    height: 3px;
  }
  .glider::-webkit-scrollbar-thumb {
    opacity: 1;
    border-radius: 99px;
    background-color: rgba(156, 156, 156, 0.25);
    box-shadow: 0 0 1px rgba(255,255,255,.25);
  }
}

/////////////////////////////////////////////////////////
// ADDITIONAL SETTINGS
/////////////////////////////////////////////////////////

.glider-slide-in {
  width: 25rem;
  height: 44.444rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  z-index: 0;
  .glider-slide-in__image {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .glider-slide-in__video {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 2;
    video {
      width: 100%;
      height: 100%;
    }
  }
  .glider-slide-in__content {
    display: flex;
    flex-direction: column-reverse;
    justify-content: baseline;
    align-items: flex-start;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 3;
    .glider-slide-in__content--text {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      background-color: v.$white;
      height: 17%;
      width: 100%;
      border-top: 1px solid gray;
      border-bottom: 1px solid gray;
      h2, p {
        font-weight: 400;
        margin: 0 0 0 1rem;
        padding: 0;
      }
      h2 {
        font-size: 2rem;
        color: v.$black;
      }
      p {
        margin-top: 1rem;
        font-size: 1.3rem;
        color: gray;
      }
    }
  }
  .glider-slide-in__button {
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
    z-index: 4;
    background-color: transparent;
  }
}

.glider-prev,
.glider-next {
  margin: 0;
  padding: 0;
}