html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Exo", sans-serif;
  font-size: 1rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.display-none {
  display: none !important;
}

p {
  font-size: 2rem;
  color: #000000;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.5rem;
}

h3 {
  font-size: 3rem;
}

img {
  width: 300px;
  height: 300px;
}

.glider-contain {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.glider {
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: none;
  transform: translateZ(0);
}

.glider-track {
  transform: translateZ(0);
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  z-index: 1;
}

.glider.draggable {
  user-select: none;
  cursor: -webkit-grab;
  cursor: grab;
}

.glider.draggable .glider-slide img {
  user-select: none;
  pointer-events: none;
}

.glider.drag {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.glider-slide {
  user-select: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  width: 100%;
  padding: 5rem 2rem;
}
.glider-slide a {
  margin: 0;
  padding: 0;
}

.glider-slide img {
  max-width: 100%;
}

.glider::-webkit-scrollbar {
  opacity: 0;
  height: 0;
}

.glider-prev, .glider-next {
  user-select: none;
  position: absolute;
  outline: none;
  background: none;
  padding: 0;
  z-index: 2;
  font-size: 4rem;
  text-decoration: none;
  left: -23px;
  border: 0;
  top: calc(50% - 2rem);
  cursor: pointer;
  color: #666;
  opacity: 1;
  line-height: 1;
  transition: opacity 0.5s cubic-bezier(0.17, 0.67, 0.83, 0.67), color 0.5s cubic-bezier(0.17, 0.67, 0.83, 0.67);
}

.glider-prev:hover,
.glider-next:hover,
.glider-prev:focus,
.glider-next:focus {
  color: #a89cc8;
}

.glider-next {
  right: -23px;
  left: auto;
}

.glider-next.disabled,
.glider-prev.disabled {
  opacity: 0.25;
  color: #666;
  cursor: default;
}

.glider-slide {
  min-width: 150px;
}

.glider-hide {
  opacity: 0;
}

.glider-dots {
  user-select: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  padding: 0;
}

.glider-dot {
  border: 0;
  padding: 0;
  user-select: none;
  outline: none;
  display: block;
  cursor: pointer;
  color: #ccc;
  border-radius: 999px;
  background: #ccc;
  width: 12px;
  height: 12px;
  margin: 7px;
}

.glider-dot:hover,
.glider-dot:focus,
.glider-dot.active {
  background: #a89cc8;
}

@media (max-width: 36em) {
  .glider::-webkit-scrollbar {
    opacity: 1;
    -webkit-appearance: none;
    width: 7px;
    height: 3px;
  }
  .glider::-webkit-scrollbar-thumb {
    opacity: 1;
    border-radius: 99px;
    background-color: rgba(156, 156, 156, 0.25);
    box-shadow: 0 0 1px rgba(255, 255, 255, 0.25);
  }
}
.glider-slide-in {
  width: 25rem;
  height: 44.444rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  z-index: 0;
}
.glider-slide-in .glider-slide-in__image {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
}
.glider-slide-in .glider-slide-in__image img {
  width: 100%;
  height: 100%;
}
.glider-slide-in .glider-slide-in__video {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 2;
}
.glider-slide-in .glider-slide-in__video video {
  width: 100%;
  height: 100%;
}
.glider-slide-in .glider-slide-in__content {
  display: flex;
  flex-direction: column-reverse;
  justify-content: baseline;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 3;
}
.glider-slide-in .glider-slide-in__content .glider-slide-in__content--text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  background-color: #FFFFFF;
  height: 17%;
  width: 100%;
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
}
.glider-slide-in .glider-slide-in__content .glider-slide-in__content--text h2, .glider-slide-in .glider-slide-in__content .glider-slide-in__content--text p {
  font-weight: 400;
  margin: 0 0 0 1rem;
  padding: 0;
}
.glider-slide-in .glider-slide-in__content .glider-slide-in__content--text h2 {
  font-size: 2rem;
  color: #000000;
}
.glider-slide-in .glider-slide-in__content .glider-slide-in__content--text p {
  margin-top: 1rem;
  font-size: 1.3rem;
  color: gray;
}
.glider-slide-in .glider-slide-in__button {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  z-index: 4;
  background-color: transparent;
}

.glider-prev,
.glider-next {
  margin: 0;
  padding: 0;
}

.header {
  padding: 3rem 7rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.header .navigation {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-basis: 64rem;
  height: auto;
}
.header .navigation .navigation__logo {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .navigation .navigation__logo img {
  height: 12rem;
  width: 12rem;
  margin: 0;
  padding: 0;
}
.header .navigation .navigation__bar {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-basis: 48rem;
}
.header .navigation .navigation__bar .navigation__bar--option {
  font-weight: 700;
  font-size: 2.4rem;
  text-transform: uppercase;
}
.header .user-option {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-basis: 27rem;
}
.header .user-option .user-option__button-light {
  margin: 0;
  padding: 0;
  height: 5rem;
  width: 12rem;
  border: 0.7rem solid #000000;
  background-color: #FFFFFF;
  font-size: 2.4rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #000000;
}
.header .user-option .user-option__button-dark {
  margin: 0;
  padding: 0;
  height: 5rem;
  width: 12rem;
  border: 0.7rem solid #000000;
  background-color: #000000;
  font-size: 2.4rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #FFFFFF;
}